import React, { useContext }  from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { AuthContext } from '../../contexts/AuthContext';
import '../../styles/header.css'; 


export default function Header() {

    const { isLoggedIn, logout } = useContext(AuthContext);
    const navigate = useNavigate();

    const handleLogout = () => {
        logout();
        navigate('/');
    };

    return (
        <div className="header">
            <div className="logo">
                <Link to="/" className="logo-link">adorable</Link>
                <div className="auth-links">
                        {isLoggedIn ? (
                            <button onClick={handleLogout} className="logout-button">로그아웃</button>
                        ) : (
                            <Link to="/login">로그인</Link>
                        )}
                </div>
            </div>
            <div className="nav-container">
                <nav className="navigation">
                    <ul className="nav-menu">
                        <li><Link to="/shelter">유기견보호센터 소개</Link></li>
                        <li><Link to="/adoft">반려견 입양</Link></li>
                        <li><Link to="/">입양후기</Link></li>
                    </ul>
                    
                </nav>
                <h1 className="header-title">Adopt a Friend for Life<br /> Find Love in a Shelter Dog<br /> Remember: Every Paw Matter!</h1>
            </div>
        </div>
    );
}
